import { register } from "../../api";

export default {
    data: function() {
        return {
            form: {
                email: null,
                username: null,
                password: null,
                dateOfBirth: null,
            },
            printables: {
                MAIL: "Adres e-mail",
                CONFIRM_MAIL: "Potwierd adres e-mail",
                PASSWORD: "Haso",
                NICK: "Nazwa uytkownika",
                SELECT_BIRTHDATE: "Zaznacz dat urodzenia",
            },
            visible: false,
        };
    },
    methods: {
        onRegister() {
            register(this.form.email, this.form.password);
        },
    },
};
